<template>
               <v-card outlined tile>
                   <v-layout wrap >
                       <v-flex
                               xs12 sm12 md8 lg8
                       >
                           <div class="ma-3">
                               <v-card-actions class="justify-center">
                                   <v-rating
                                           readonly
                                           dense
                                           half-increments
                                           v-model="rating"
                                           color="yellow darken-3"
                                           background-color="grey darken-1"
                                           hover
                                           size="28"
                                   ></v-rating>
                                   <h3 class="warning--text ml-1 mt-1">{{ rating ? rating : '' }}</h3>
                                   <span class="grey--text mx-2 mt-1">
                                 ({{ portfolio.reviews.length }})
                                    </span>
                               </v-card-actions>
                               <v-divider />
                               <h2 class=" font-weight-bold">{{ portfolio.title }}</h2>
                               <p v-html="portfolio.description"></p>
                               <h2 class="mb-2">ປະເພດ</h2>
                               <v-btn @click="filterCategory(portfolio.categoryId._id)"
                                      color="primary"
                                      outlined
                                      class="font-weight-bold">
                                   {{ portfolio.categoryId ?  portfolio.categoryId.name : '' }}
                               </v-btn>
                               <h2 class=" my-3 ">Reviews ({{ portfolio.reviews.length }})</h2>
                               <v-card class="my-1" outlined v-for="review in reviews.slice(0, reviewToShow)"
                                       :key="review.index">
                                   <v-card-actions>
                                       <v-avatar>
                                           <v-img :src="$server + review.userId.image"></v-img>
                                       </v-avatar>
                                       <span class="ml-1"> {{ review.text }}</span>
                                       <v-spacer></v-spacer>
                                       <v-rating
                                               readonly
                                               dense
                                               half-increments
                                               v-model="review.rating"
                                               color="yellow darken-3"
                                               background-color="grey darken-1"
                                               hover
                                               size="25"
                                       ></v-rating>
                                   </v-card-actions>
                               </v-card>
                               <div class="text-center">
                                   <v-btn
                                           v-if="reviews.length > reviewToShow"
