                                           color="primary"
                                           outlined
                                           @click="loadmore">Load more..
                                       .</v-btn>
                               </div>
                               <br>
                           </div>
                       </v-flex>
                       <v-flex
                               xs6 sm6 md4 lg4 class="hidden-sm-and-down">
                           <app-avatar class="mx-2 my-2" :user="user" :auth="auth" :portfolio="portfolio"></app-avatar>
                       </v-flex>
                   </v-layout>
               </v-card>
</template>

<script>
    import appAvatar from '../../components/detail/Avatar'
    export default {
        components: {
            appAvatar
        },
        props: [
            'portfolio',
            'user',
            'auth',
            'filterCategory',
            'rating',
            'reviews'
        ],
        data: () => ({
            reviewToShow: 1,
            loading: false
        }),
        methods: {
             loadmore () {
                 this.loading = true
                 setTimeout (() => {
                     this.reviewToShow += 2
                     this.loading = false
                 }, 100)

            }
        }
    }
</script>
